#menubar {
	margin: 0;
	padding: 0;
	}

#menubar li {
	float: left;
	list-style: none;
	}

#menubar li a {
	display: block;
	text-decoration: none;
	white-space: nowrap;
	text-align: left;
	}

#menubar li a:hover {
	text-decoration: underline;
	}

#menubar li ul {
	z-index: 2;
	margin: 10px -10px;
	padding: 0;
	position: absolute;
	visibility: hidden;
	box-shadow: 2px 2px 2px #888;
	}

#menubar li ul li {
	float: none;
	display: inline;
	}

#menubar li ul li a {
	width: auto;
	background: #2b6a5a;
	color: #c3c861;
	padding: 5px 12px;
	}

#menubar li ul li a:hover {
	background: #c4c1b8;
	color: #2b6a5a;
	}

#menubar .active a {
	background: #c4c1b8;
	color: #2b6a5a;
	}

#menubar .active a:hover {
	background: #2b6a5a;
	color: #c4c1b8;
	}